// 用于定义发起网络请求的方法
// 每个方法只负责一个URL地址
import request from "@/utils/request";
import store from "@/store";
// 用户登录
export const loginAPI = (data) => {
  return request({
    url: "/user/login",
    method: "post",
    data,
  });
};
// 获取用户信息
export const getUserInfoAPI = () => {
  return request({
    url: "/user/info",
    headers: {
      Authorization: store.state.token,
    },
  });
};
// 获取食堂列表
export const getRoomListAPI = (data) => {
  return request({
    url: "/room/mlist",
    method: "POST",
    data,
  });
};
